@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: calc(.4rem + 1vw);
}
:root{
  --gray: #333;
  --softgray: #eee;
}
.container{
  min-height: 100vh;
  overflow-y: hidden;
}
.page{
  position: absolute;
  height: 100%;
  width: 100%;
}
.page-enter {
  opacity: 0;
  -webkit-transform: scale(.95) translateX(-10%);
          transform: scale(.95) translateX(-10%);
}

.page-enter-active {
  opacity: 1;  
  -webkit-transform: scale(1) translateX(0%);  
          transform: scale(1) translateX(0%);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;  
}

.page-exit {
  opacity: 1;
}

.page-exit-active {
  opacity: 0;
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
  -webkit-transform: translateX(10%);
          transform: translateX(10%);
}
/* nav */
nav {
    background-color: #f5f5f5af;
    box-shadow: 0 0 3px #c3c3c3,0 0 5px #e4e4e4;
    color: var(--gray);
    height: 10%;
    position: relative;
    display: -webkit-flex;
    display: flex;
    place-items: center;
}
/* .logo-wrap{
} */
.logo{
    font-family: 'New Tegomin', serif;
    font-size: calc(1rem + 1vw);
    font-weight: bold;
    padding: 0;
    margin: 1vw;
    vertical-align: super;
    -webkit-text-decoration: 0;
            text-decoration: 0;
    color: black;

}
.bold{
    font-weight: bold;
}
nav .menu a {
    -webkit-text-decoration: 0;
            text-decoration: 0;
    padding: 0vw;
    margin: 0 1vw;
    text-transform: capitalize;
    font-size: 1rem;    
}
nav .menu a:hover {
    color: black;
}
.menu a {
    color: #333;

}
.serach-box{
    -webkit-flex: 1 1;
            flex: 1 1;
}
.serach-box input {
    padding: .05vw 1vw;
    outline: 0;
    margin: .25vw 1vw .25vw 0;
    float: right;
    border: 1px solid rgb(236, 236, 236);
    box-shadow: 0 0 .05vw #333;
    color: #333;
    border-radius: .5vw;
    background: transparent
}
.table-wrap{
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0 auto;
    height: 80%;
    width: 80%;
}
/* table */
.top100-table{
    position: relative;
    height: 100%;
    width: 100%;
    border-collapse: collapse;
  }
  table tr{
    color: #333;
    border-bottom: 1px solid rgb(216, 216, 216);
    text-transform: uppercase;
  }
  
  table tr:hover{
    background-color: #eeeeee;
    cursor: pointer;
  }
  table td:first-child {
    overflow: auto;
    margin: 0;
    min-width: 100px;
    max-width: calc(8rem + 8vw);
  }
  table td:first-child::-webkit-scrollbar {
    width: .1rem;
    height: .5vw;
  }
  
  table td:first-child::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  }
  
  table td:first-child::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
  }
  table td img {
    width: calc(1rem + 4vw);
    height: calc(1rem + 4vw);
    margin: auto 0;
  }
  .coin-name-div{
    display: -webkit-flex;
    display: flex;
  }
  .coin-name-div h5 {
    font-size: calc(.5rem + 1vw);
  }
  .coin-name-div,.coin-name-div p {
    padding: .25vw 1vw;
  }
  .coin-name-div p span {
    display: inline-block;
    text-transform: uppercase;
    padding: .25vw 1vw;
    border-radius: .5vw;
    font-size: calc(.5rem + .8vw);
  
  }
  .coin-name-div p span:first-child{
    background-color: #333;
    color: #eee;
  }
  @-webkit-keyframes anima {
    from {
      font-size: 3vw;
      opacity: 1;
    }
    to {
      -webkit-transform: scale(3);
              transform: scale(3);    
      opacity: 0;
    }
  }
  @keyframes anima {
    from {
      font-size: 3vw;
      opacity: 1;
    }
    to {
      -webkit-transform: scale(3);
              transform: scale(3);    
      opacity: 0;
    }
  }
.homepage{
    position: relative;
    height: 90vh;
}
.global-data-section{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    text-align: center;
    background-color: #f5f5f5af;
    color: #333;
    margin: 1vw auto;
    border-radius: 1vw;
